import React, { PureComponent } from "react";
import ReactPlayer from "react-player"
import { isTagShop } from "../../../../actions/api"; 
import { reelPlayTimeUpdateData } from "../../../../utils/Common";

export default class ReelVideo extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            loadError: false,
            paddingBottom: 100,
            videoLoaded: false,
            played: 0
        }
    }
    setVideoLoaded = event => {  
        this.setState({
            videoLoaded: event
        })
    }
 
    componentWillUnmount() {
        const { played } = this.state
        if (isTagShop) reelPlayTimeUpdateData(played)
    }

    onUpdateTime = (progress) => {   
        try {
            if (isTagShop) this.setState({ played: this.state.played + progress.playedSeconds })
        }
        catch (e) {
            console.log(e)
        }
    }

    render() {
        const { VideoClass, Post, controls, autoPlay, handleVideoEnded, muted } = this.props
        const { videoLoaded } = this.state
        const { media } = Post
        const { height, width} = 100
        const videoStyle = {
            paddingBottom: `100vh`,
        };

        return <div className={`${VideoClass}_wrap_`} style={videoStyle}>
            <ReactPlayer
                className={`${VideoClass} tb_media-${Post.id}`}
                url={media.video.full}
                data-height={height}
                data-width={width}
                data-type="video"
                data-network={Post.networkId}
                data-link={Post.link}
                data-item-id={Post.id}
                data-load={0}
                config={{
                    facebook: {
                        appId: '117029975354072',
                        attributes: {
                            'data-width': width,
                            'data-height': height,
                            poster: media.image.small
                        }
                    },
                    file: {
                        attributes: {
                            preload: 'metadata',
                            poster: media.image.small
                        }
                    }
                }}
                loop={true}
                muted={muted}
                volume={autoPlay ? 1 : 0}
                playsinline={true}
                onReady={(e) => { this.setVideoLoaded(true) }}
                playing={autoPlay}
                height={'100%'}
                width={'100%'}
                controls={controls}
                onProgress={this.onUpdateTime}
                onEnded={handleVideoEnded ? handleVideoEnded : null}
            />
            {!videoLoaded ? <div className='tb_rpp_v_spinner'><div></div></div> : null}
        </div>
    }
}



